.navbar {
	background: linear-gradient(90deg,
			rgb(28, 27, 27) 0%,
			rgb(26, 23, 23) 100%);
	height: 80px;
	position: fixed !important;
	width: 100% !important;
	z-index: 2000 !important;
	top: 0% !important;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.2rem;
}

.navbar-logo {
	color: #fff;
	justify-self: start;
	margin-left: 0em;
	cursor: pointer;
	text-decoration: none;
	font-size: 2rem;
	padding-bottom: 21px;
	font-weight: 600;
}

.navbar-logo img {
	max-width: 130px;
}

.fa-firstdraft {
	margin-left: 0.5rem;
	font-size: 1.6rem;
}

.nav-menu {
	display: grid;
	grid-template-columns: repeat(5, auto);
	grid-gap: -26px;
	list-style: none;
	text-align: center;
	width: 33vw;
	margin-right: 46rem;
	font-weight: 600;
}

.nav-item {
	display: flex;
	align-items: center;
	height: 80px;
}

.nav-item #notification-numb {
	color: black;
}

.nav-links {
	color: white;
	text-decoration: none;
	padding: 0.5rem 1rem;
	width: max-content;
}

.nav-links:hover {
	background-color: #1888ff;
	border-radius: 4px;
	transition: all 0.2s ease-out;
}

.fa-bars {
	color: #fff;
}

.nav-links-mobile {
	display: none;
}

.menu-icon {
	display: none;
}

.current-user {
	padding-top: 20px;
	padding-left: 0px;
	/* width: 10em; */
}

.current-user .adventurer {
	font-weight: 400;
	color: mintcream;
	font-family: 'Courier New', Courier, monospace;
	font-style: italic;
	margin-left: -4em;
}

.current-user .adventurer-name {
	font-weight: 400;
	color: mintcream;
	font-family: Georgia, 'Times New Roman', Times, serif;
	padding-left: 0px;
	padding-right: 0px;
	cursor: pointer;
	margin-right: 106px;
}

.current-user .adventurer-name:hover {
	background-color: #0d6efd;
	border-radius: 4px;
	transition: all 0.2s ease-out
}

.current-user button {
	margin-left: 15em;
	margin-top: -68.5px;
	width: 7em;
	font-family: sans-serif;
}

/* Mobile Serach App */
.mobile-search-app {
	display: none;
}

.current-user-mobile {
	display: none;
	padding-top: 20px;
	padding-left: 0px;
}

.current-user-mobile .adventurer-name {
	/* display: none; */
	font-weight: 400;
	color: mintcream;
	font-family: Georgia, 'Times New Roman', Times, serif;
	padding-left: 0px;
	padding-right: 25px;
	cursor: pointer;
	text-decoration: underline;
}

.current-user-mobile .adventurer-name:hover {
	/* display: none; */
	color: rgb(243, 239, 8);
}

.current-user-mobile button {
	/* display: none; */
	margin-left: 18em;
	margin-top: -68.5px;
	width: 7em;
	font-family: sans-serif;
}

.no-user-mobile .login-nav-btn {
	display: none;
}

.login-nav-btn a {
	text-decoration: none !important;
}

.user-null {
	padding-top: 20px;
	padding-left: 10px;
	width: 10em;
}

.user-null span {
	font-weight: 400;
	color: mintcream;
}

.user-null button {
	margin-left: 13em;
	margin-top: -60px;
	width: 7em;
	font-family: sans-serif;
}

.user-null a {
	text-decoration: none;
}